import uniIcon from '@/components/uni-icon/uni-icon.vue';
import { post } from '@/api/user.js'
import { api } from '@/config/common.js'
export default {
	data() {
		return {
			loading: false,
			stop: false,
			miao: 60,
			loginname: '',
			telephone: '',
			code: '',
			password: '',
			re_password: '',
			title: '注册',
			promoterid:0
		};
	},
	components: { uniIcon },
	onLoad() {
		if(uni.getStorageSync('promoterid')){
			this.promoterid = uni.getStorageSync('promoterid');
			console.log(this.promoterid);
		}else{
			console.log('没有推荐人');
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.switchTab({
				url:'/pages/my/my'
			}) 
		},
		login_action(){
			uni.reLaunch({
				url:'/pages/my/login/login'
			})
		},
		handleReg() {
			const { loginname, password ,telephone,re_password,promoterid} = this;
			if (loginname.length == 0) {
				this.$api.msg('登录账号不能为空！');
				return;
			}
			if (password.length == 0) {
				this.$api.msg('登录密码不能为空！');
				return;
			}
			if (re_password.length == 0) {
				this.$api.msg('重复密码不能为空！');
				return;
			}
			if(password!=re_password){
				this.$api.msg('两次密码不一致！');
				return;
			}
			if (telephone.length != 11) {
				this.$api.msg('手机号码不正确！');
				return;
			}
			const sendData = {
				loginname,
				password,
				telephone,
				promoterid
			};
			this.loading = true;		
			post(api.Regist,sendData).then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
					let userInfo = {
									"token":res.data.data.token,
									"exp":res.data.data.exp,
									"userId":res.data.data.userId,
								};
								uni.setStorage({
								    key: 'userInfo',
								    data: userInfo,
								    success: function () {
								        uni.switchTab({
								        	url:'/pages/index/index'
								        }) 
								    }
								});
					} else {
						this.$api.msg(res.data.returnMessage) 
					}
					this.loading =false;
				}).catch(error => {
					this.loading =false;
					this.$api.msg('请求失败fail') 
				})
			
			
		},
		send() {
			const mobile = this.mobile;
			this.settime(60);
			if (mobile.length != 11) {
				this.$api.msg('手机号错误');
				return;
			}
		},
		settime(smiao) {
			const that = this;
			const miao = that.miao;
			if (miao == 0) {
				that.miao = 0;
				this.stop = false;
			} else {
				setTimeout(() => {
					smiao--;
					this.miao = smiao;
					that.settime(smiao);
				}, 1000);
			}
		}
	}
};
